*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  min-width: 320px;
  scroll-behavior: smooth;
  background-color: #fff;
  font-family: Roboto, sans-serif;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.container, .movies__container {
  min-width: 320px;
  max-width: 1024px;
  margin: 0 auto;
}

#loader {
  width: 60px;
  display: none;
  position: absolute;
}

.noScroll {
  height: auto;
  position: static;
  overflow: hidden;
}

.header--index-background {
  background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-320px-1x.5ce2f996.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header--library-background {
  background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-320px-1x.ed45b3f6.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header {
  background-color: inherit;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header__container {
  padding: 46px 20px 43px;
}

.header__nav {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__search {
  width: 100%;
  border-bottom: 1px solid #fff;
  justify-content: center;
  align-items: center;
  margin-top: 38px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__search:hover, .header__search :focus {
  border-color: #ff6b08;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.header__error {
  color: #ff001b;
  margin-top: 22px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.header__error--hidden {
  visibility: hidden;
}

.header__buttons {
  justify-content: center;
  gap: 20px;
  margin-top: 45px;
  margin-bottom: 20px;
  display: flex;
}

.logo {
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
  text-decoration: none;
  display: flex;
}

.logo__svg {
  width: 24px;
  height: 24px;
}

.logo__name {
  color: #fff;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.17;
  display: none;
}

.navigation {
  justify-content: flex-end;
  align-items: center;
  gap: 39px;
  display: flex;
}

.navigation__item {
  height: 24px;
  text-transform: uppercase;
  color: #fff;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.navigation__item--mark {
  position: relative;
}

.navigation__item--mark:after {
  content: "";
  width: 100%;
  height: 3px;
  cursor: default;
  pointer-events: none;
  background-color: #ff6b08;
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.navigation__item:hover, .navigation__item :focus {
  color: #ff6b08;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.search__input {
  width: 100%;
  background-color: inherit;
  color: #fff;
  border: none;
  outline: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.search__input::-ms-input-placeholder {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.search__input::placeholder {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.search__button {
  background-color: inherit;
  cursor: pointer;
  border: none;
  padding: 5px 5px 2px;
}

.search__button:hover .search__svg, .search__button :focus .search__svg {
  stroke: #ff6b08;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.search__svg {
  fill: none;
  stroke: #fff;
  width: 12px;
  height: 12px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.watched-button, .queue-button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: inherit;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 14px 36px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.watched-button:hover, .watched-button:focus, .queue-button:hover, .queue-button:focus {
  background-color: #ff6b01;
  border-color: #ff6b01;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
  box-shadow: 4px 4px 8px rgba(255, 107, 1, .6);
}

.watched-button--active, .queue-button--active {
  background-color: #ff6b01;
  border-color: #ff6b01;
}

@media (min-resolution: 2x) {
  .header--index-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-320px-2x.d7a9bcee.png");
  }

  .header--library-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-320px-2x.a8753ad0.png");
  }
}

@media screen and (min-width: 768px) {
  .header--index-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-768px-1x.49deeb5a.png");
  }

  .header--library-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-768px-1x.e4f77ecd.png");
  }

  .header__container {
    padding: 41px 74px 59px;
  }

  .logo__name {
    display: block;
  }

  .header__search {
    width: 50%;
  }
}

@media screen and (min-width: 768px) and (min-resolution: 2x) {
  .header--index-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-768px-2x.35dff73f.png");
  }

  .header--library-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-768px-2x.55e1ad9d.png");
  }
}

@media screen and (min-width: 1024px) {
  .header--index-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-1024px-1x.e930ef5d.png");
  }

  .header--library-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-1024px-1x.e4f77ecd.png");
  }

  .header__search {
    width: 40%;
  }
}

@media screen and (min-width: 1024px) and (min-resolution: 2x) {
  .header--index-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-index-1024px-2x.e53072e5.png");
  }

  .header--library-background {
    background-image: linear-gradient(rgba(0, 0, 0, .56) 0%, rgba(0, 0, 0, .56) 100%), url("header-library-1024px-2x.55e1ad9d.png");
  }
}

.movies {
  min-height: calc(100vh - 395px);
}

.movies__container {
  padding: 20px 20px 40px;
}

.movies__list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  list-style: none;
  display: flex;
}

.movie {
  max-width: 500px;
  cursor: pointer;
  border-radius: 5px;
  flex-direction: column;
  flex-basis: calc(100% - 30px);
  padding: 5px;
  font-size: 12px;
  font-weight: 500;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.movie:hover, .movie:focus {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.05);
  box-shadow: 0 0 20px -5px #ff7402;
}

.movie__img {
  border-radius: 5px;
  flex-grow: 1;
  margin-bottom: 10px;
}

.movie__title {
  font-size: inherit;
  font-weight: inherit;
  text-transform: uppercase;
  padding-bottom: 5px;
  padding-left: 5px;
  line-height: 16px;
}

.movie__info {
  gap: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  display: flex;
}

.movie__detail {
  color: #ff6b08;
  line-height: 14px;
}

@media screen and (min-width: 768px) {
  .movies__container {
    padding: 60px 75px 50px;
  }

  .movies__list {
    justify-content: flex-start;
  }

  .movie {
    max-width: 400px;
    flex-basis: calc(50% - 15px);
  }
}

@media screen and (min-width: 1024px) {
  .movies__container {
    padding: 60px 71px 50px;
  }

  .movie {
    flex-basis: calc(33.3333% - 20px);
  }
}

.pagination {
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 40px 20px;
  display: flex;
}

.pagination__button {
  width: 40px;
  height: 40px;
  background-color: inherit;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 5px;
  padding-top: 3px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.17;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__button:hover, .pagination__button :focus {
  border: 1px solid #ff6b08;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__button--actual {
  color: #fff;
  background-color: #ff6b08;
}

.pagination__button--next {
  background-color: #f7f7f7;
  background-image: url("arrow-right.117fcf62.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-left: 10px;
}

.pagination__button--next:hover, .pagination__button--next:focus {
  background-color: #ff6b01;
  border-color: #ff6b01;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 4px 4px 8px rgba(255, 107, 1, .6);
}

.pagination__button--previous {
  background-color: #f7f7f7;
  background-image: url("arrow-left.61a54a7f.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 10px;
}

.pagination__button--previous:hover, .pagination__button--previous:focus {
  background-color: #ff6b01;
  border-color: #ff6b01;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 4px 4px 8px rgba(255, 107, 1, .6);
}

.pagination__button--hideAbility {
  display: none;
}

.pagination__button--deactive {
  pointer-events: none;
}

.pagination__button--deactive:hover, .pagination__button--deactive :focus {
  border: none;
}

@media screen and (min-width: 768px) {
  .pagination__button--hideAbility {
    display: block;
  }
}

.footer {
  min-width: 320px;
  background-color: #f7f7f7;
  padding-top: 28px;
  padding-bottom: 30px;
}

.copyright {
  text-align: center;
  min-width: 320px;
  padding-left: 46px;
  padding-right: 46px;
}

.copyright__svg {
  width: 14px;
  height: 12px;
  fill: #ff6b08;
  display: inline-block;
}

.copyright__span {
  cursor: pointer;
  text-decoration: underline;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.copyright__span:hover, .copyright__span :focus {
  color: #ff6b08;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.copyright p {
  color: #545454;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .copyright p {
    font-size: 16px;
  }
}

.modal {
  opacity: 1;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  height: 100%;
  width: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, .5);
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.modal__hidden {
  opacity: 0;
  display: none;
}

.modal__button {
  width: 22px;
  height: 22px;
  cursor: pointer;
  background: none;
  border: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal__button:hover {
  fill: #ff6b01;
  transform: scale(1.4);
}

.modal__button > svg {
  height: 22px;
  width: 22px;
}

.modal__popup {
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 35px;
  position: relative;
}

.modal__gallery {
  border-radius: 20px;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 15px;
  list-style: none;
  display: flex;
}

.modal__text {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  font-size: 26px;
  display: flex;
}

.modal__text-mein {
  color: #ff6b08;
  text-shadow: 5px 3px 2px #000;
  margin: 5px;
  font-size: 42px;
}

.modal__card {
  transform-style: preserve-3d;
  transition: transform 1.5s;
}

.modal__card:hover {
  cursor: pointer;
  transform: rotateY(180deg);
}

.modal__thumbnail, .modal__back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 3px solid #ff6b08;
}

.modal__thumbnail {
  cursor: pointer;
  height: 139px;
  width: 130px;
  border: 3px solid #ff6b08;
  border-radius: 15px;
  margin: 5px;
  overflow: hidden;
  box-shadow: 0 0 48px 2px rgba(0, 0, 0, .5);
}

.modal__back {
  height: 139px;
  width: 130px;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 16px;
  font-size: 13px;
  display: flex;
  position: fixed;
  top: 5px;
  left: 0;
  transform: rotateY(180deg);
  box-shadow: 0 0 48px 2px rgba(0, 0, 0, .5);
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 100;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, .7);
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: visibility .25s ease-in-out, opacity .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.overlay--is-hidden {
  visibility: hidden;
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .overlay {
    width: 100vw;
    display: flex;
  }
}

.modal__window {
  width: 100%;
  min-width: 280px;
  background-color: #fff;
  padding: 48px 20px 40px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25), 0 4px 4px rgba(0, 0, 0, .25);
}

@media screen and (min-width: 768px) {
  .modal__window {
    width: 618px;
    padding: 40px 30px;
  }
}

@media screen and (min-width: 1024px) {
  .modal__window {
    width: 882px;
    padding: 50px 30px 40px;
  }
}

.modal__button-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  justify-content: center;
  margin: 0;
  padding: 0;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__button-close:hover {
  fill: #ff6b01;
  transform: scale(1.4);
}

.modal__movie {
  min-width: 100%;
  height: 100%;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 0 20px -5px #ff7402;
}

.modal-movie__container {
  min-width: 100%;
  height: 100%;
  cursor: auto;
  flex-direction: column;
  gap: 30px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .modal-movie__container {
    flex-direction: row;
  }
}

.modal-movie__picture {
  height: 100%;
  border-radius: 5px;
}

@media screen and (min-width: 768px) {
  .modal-movie__picture {
    width: 264px;
  }
}

@media screen and (min-width: 1024px) {
  .modal-movie__picture {
    width: 326px;
  }
}

.modal-movie__information {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.modal-movie__title {
  text-transform: uppercase;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.15;
}

@media screen and (min-width: 1024px) {
  .modal-movie__title {
    font-size: 30px;
  }
}

.modal-movie__table {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  font-family: Roboto;
  font-size: 12px;
