  }
}

.modal-table__number {
  border-radius: 5px;
  padding: 1px 9px;
}

.modal-table--orange {
  color: #fff;
  background-color: #ff6b01;
}

.modal-table--grey {
  background-color: #f7f7f7;
}

.modal-table--uppercase {
  text-transform: uppercase;
}

.modal-movie__overview {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.modal-movie__description {
  text-align: left;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.7;
}

.modal-movie__buttons {
  flex-direction: row;
  justify-content: center;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .modal-movie__buttons {
    justify-content: flex-start;
  }
}

.modal-movie__btn {
  max-width: 125px;
  min-height: 44px;
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid;
  border-radius: 5px;
  margin: 0;
  padding: 14px 9px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-movie__btn.is-active {
  box-shadow: px 4px 43px rgba(255, 107, 1, .6);
  background-color: #ff6b01;
  border: #ff6b01;
}

.modal-movie__btn:hover, .modal-movie__btn:focus {
  background-color: #ff6b01;
  border-color: #ff6b01;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.05);
  box-shadow: 4px 4px 8px rgba(255, 107, 1, .6);
}

@media screen and (max-width: 1279px) {
  .modal-movie__btn {
    width: 117px;
